body {
  background-color: #d3d3d3;
}

.loader-text {
  flex-direction: column;
  align-items: center;
  display: flex;
}

.hidden {
  display: none;
}

.loader {
  width: 160px;
  height: 185px;
  background: #fff;
  border-radius: 100px 100px 0 0;
  margin-top: 10px;
  display: flex;
  position: relative;
}

.loader:after {
  content: "";
  width: 100px;
  height: 125px;
  background-image: radial-gradient(circle, #000 48%, rgba(0, 0, 0, 0) 55%), radial-gradient(circle, #000 48%, rgba(0, 0, 0, 0) 55%), radial-gradient(circle, #fff 30%, rgba(0, 0, 0, 0) 45%), radial-gradient(circle, #000 48%, rgba(0, 0, 0, 0) 51%), linear-gradient(#000 20px, rgba(0, 0, 0, 0) 0), linear-gradient(#cfecf9 60px, rgba(0, 0, 0, 0) 0), radial-gradient(circle, #cfecf9 50%, rgba(0, 0, 0, 0) 51%), radial-gradient(circle, #cfecf9 50%, rgba(0, 0, 0, 0) 51%);
  background-position: 25px 10px, 55px 10px, 36px 44px, 50% 30px, 50% 85px, 50% 50px, 50% 22px, 50% 45px;
  background-repeat: no-repeat;
  background-size: 16px 16px, 16px 16px, 10px 10px, 42px 42px, 12px 3px, 50px 25px, 70px 70px, 70px 70px;
  animation: faceLift 3s linear infinite alternate;
  position: absolute;
  top: 25px;
  left: 50%;
  transform: translateX(-50%);
}

.loader:before {
  content: "";
  width: 140%;
  height: 125px;
  background-image: radial-gradient(circle, #fff 48%, rgba(0, 0, 0, 0) 50%), radial-gradient(circle, #fff 48%, rgba(0, 0, 0, 0) 50%);
  background-position: 0 12px, 145px 12px;
  background-repeat: no-repeat;
  background-size: 65px 65px;
  animation: earLift 3s linear infinite alternate;
  position: absolute;
  top: 0;
  left: -20%;
}

@keyframes faceLift {
  0% {
    transform: translateX(-60%);
  }

  100% {
    transform: translateX(-30%);
  }
}

@keyframes earLift {
  0% {
    transform: translateX(10px);
  }

  100% {
    transform: translateX(0);
  }
}

.cat-info {
  gap: 10px;
  margin-top: 20px;
  display: flex;
}

.cat-title {
  font-size: 36px;
}

.cat-text, .cat-temperaments {
  font-size: 20px;
}

